.delivery_history_detail_modal {
  //   width: 100px;
  //   height: 100px;
  //   background-color: red;
  display: flex;
  align-items: center;
  justify-content: center;

  .icon_close {
    color: red;
  }

  .modal-header {
    margin: 20px 20px;
    display: flex;
    justify-content: flex-end;
  }

  .delivery_history_detail_modal_container {
    width: 800px;
    height: 700px;
    background-color: white;
    overflow: scroll;
  }
  .title {
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 1.7rem;
  }

  .delivery_history_detail_modal_content {
    display: flex;
    flex-direction: row;
    padding: 30px;
  }
  .info_merchant_container {
    display: flex;
    align-items: center;
  }

  .delivery_address_container {
    margin-top: 12px;
  }

  .image_merchant {
    width: 66px;
    height: 66px;
    border-radius: 33px;
    box-shadow: 0px 5px 16px rgba(0, 0, 0, 0.25);
  }
  .merchant_name {
    margin-left: 20px;
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 1.2rem;
  }
  .address {
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 1.1rem;
  }

  .delivery_info_content_item {
    margin-top: 12px;
    display: flex;
    align-items: center;
  }

  .booking_date {
    margin-top: 12px;
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 1.1rem;
  }

  .receive_time_title {
    margin-left: 20px;
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 1.1rem;
  }

  .receive_time_content {
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 1.1rem;
  }

  .icon {
    fill: #dadada;
  }

  .note_title {
    margin-left: 20px;
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 1.1rem;
  }

  .note_content {
    margin-top: 12px;
    border-radius: 4px;
    padding: 4px;
    border: 1px solid #d5d6db;
  }
  .delivery_food_title {
    margin-left: 20px;
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 1.2rem;
  }

  .delivery_food_title_container {
    display: flex;
    align-items: center;
    margin: 12px 0;
  }

  .food_container {
    display: flex;
    margin-bottom: 10px;
  }
  .food_image {
    width: 78px;
    height: 50px;
    border-radius: 4px;
  }
  .food_quantity {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    margin-left: 16px;
    width: 30px;
    font-size: 15px;
  }

  .food_name {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    margin-left: 16px;
    font-size: 15px;
    flex: 1;
  }
  .food_price {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    margin-left: 10px;
    font-size: 15px;
  }

  .divider_line {
    width: 1px;
    background-color: #adb1b9;
    // height: 100%;
    margin-left: 10px;
    margin-right: 10px;
  }

  .qr_code_content {
    width: 293px;
    height: 293px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    border: 1px solid #adb1b9;
    padding: 16px;
  }

  .qr_code_value,
  .qr_code_title {
    font-family: 'Google Sans', sans-serif;
    font-weight: 700;
    font-size: 1.2rem;
  }

  .payment_type_container,
  .total_money_container,
  .promotion_container,
  .shipping_fee_container,
  .temporary_fee_container {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
  }

  .temporary_fee_title {
    font-family: 'Google Sans', sans-serif;
    font-weight: 700;
    font-size: 1.1rem;
  }
  .temporary_fee_content,
  .temporary_fee_food_quantity {
    font-family: 'Google Sans', sans-serif;
    font-weight: 700;
    font-size: 1.1rem;
    color: #ff4355;
  }

  .payment_type_title,
  .promotion_title,
  .delivery_fee_title {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    font-size: 1rem;
  }

  .delivery_distance_content,
  .payment_type_content,
  .promotion_code,
  .delivery_distance {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    font-size: 1rem;
    color: #ff4355;
  }
  .promotion_discount_content,
  .delivery_distance_content {
    color: black;
  }
  .promotion_discount_content {
    text-decoration: line-through;
  }

  .qr_code_container {
  }

  .diver_fee_line {
    width: 100%;
    height: 1px;
    background-color: #adb1b9;
    margin-top: 10px;
  }

  .cancel_delivery_button {
    margin-top: 20px;
    width: 160px;
    background: #ffffff;
    border: 1px solid #ff4355;
    box-sizing: border-box;
    border-radius: 5px;
    padding: 12px 0px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 700;
    font-size: 1.2rem;
    color: #ff4355;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .reorder_delivery_button {
    margin-top: 20px;
    width: 160px;
    background: #ff4355;
    border-radius: 5px;
    padding: 12px 0px;
    font-family: 'Google Sans', sans-serif;
    font-weight: 700;
    font-size: 1.2rem;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .group-bottom-button {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  @media screen and(max-width:900px) {
    .delivery_history_detail_modal_container {
      width: 500px;
    }
    .delivery_history_detail_modal_content {
      flex-direction: column;
    }
    .qr_code_content {
      margin: 0px auto;
    }
    .qr_code_container {
      width: 100%;
    }
  }
}
