
.item-voucher-modal-container {
  display: flex;
  width: 100%;
  flex-direction: column;
  .line {
    display: flex;
    width: 100%;
    height: 0.5px;
    margin: 10px 6%;
    align-self: center;
    background-color: #EFF0F3;
  }
  .title {
    display: flex;
    justify-content: center;
    p {
      font-size: 23px;
      line-height: 29px;
    }
  }
  .box-date {
    margin: 12px 0px 0px 0px;
    align-items: center;
    .text-date {
      color: #c4a732;
      font-size: 17px;
      line-height: 21px;
      margin-right: 14px;
    }
  }
  .contain {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .left-contain {
      display: flex;
      flex-direction: column;
      height: 56px;
      .text-expiry-date {
        font-size: 12px;
        line-height: 15px;
        text-align: justify;
        color: #858688;
      }
      .text-code {
        font-size: 15px;
        line-height: 19px;
        font-weight: 700;
        text-transform: uppercase;
        margin: 6px 10px 6px 0px;
      }
      .box-code {
        display: flex;
        align-items: center;
      }
    }
    .right-contain {
      display: flex;
      .custom-button-style {
        margin: 0px 2px;
        height: 36px;
        &.delete {
          display: flex;
          background-color: var(--white);
          color: var(--primary-pink);
          width: 48px;
          border: 1px solid var(--primary-pink);
        }
        &.use {
          background-color: var(--primary-pink);
          color: var(--white);
          width: 90px;
        }
      }

      .custom-button-title {
        font-size: 13px;
        line-height: 18px;
        &.delete {
          color: var(--primary-pink);
          width: 48px;
        }
        &.use {
          color: var(--white);
          width: 90px;
        }
      }
    }
  }
}
.item-voucher-icon{
  padding: 10px;
}