.item-voucher {
  display: flex;
  flex-direction: column;
  margin-bottom: 1.5em;
  // width: 370px;
  box-shadow: 0px 4px 11px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  padding: 16px;
  .title {
    font-family: 'Google Sans', sans-serif;
    font-weight: 600;
    font-size: 17px;
  }

  .voucher_name {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    font-size: 17px;
  }
  .group-top-content {
    display: flex;
    width: 100%;
  }
  .group-bottom-button {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding-top: 10px;
  }
  .icon-close {
    width: 24px;
    height: 24px;
  }
  .badge {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    width: 24px;
    height: 24px;
    border-radius: 24px;
    align-items: center;
    justify-content: center;
    background-color: var(--primary-peach);
    p {
      font-size: 12px;
      line-height: 20px;
      color: var(--white);
    }
  }
  .box-img {
    position: relative;
    margin-top: 4;
    justify-content: center;
    align-items: center;
    display: flex;
  }
  .img {
    height: 72px;
    width: 72px;
    border-radius: 36px;
    object-fit: contain;
    box-shadow: 0px 4px 11px rgba(0, 0, 0, 0.25);
  }
  .box-content {
    flex: 1;
    margin-left: 38px;
    h1 {
      font-size: 15px;
      line-height: 18px;
      color: #000000;
      font-weight: 600;
      padding-bottom: 6px;
    }
    p {
      font-size: 15px;
      line-height: 18px;
      text-transform: uppercase;
      color: #000000;
      font-weight: 600;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2; /* number of lines to show */
      -webkit-box-orient: vertical;
    }
  }
  .button-use {
    width: 160px;
    &-title {
      font-size: 15px;
      line-height: 18px;
    }
  }
  .button-detail {
    margin-left: 10px;
    width: 160px;
    background-color: var(--white);
    border: 1px solid var(--primary-yellow-dark);
    &-title {
      font-size: 15px;
      line-height: 18px;
      color: var(--primary-yellow-dark);
    }
  }
}

.box-empty {
  display: flex;
  margin-bottom: 8px;
  width: 370px;
  background-color: transparent;
}
