.code-required-container {
  display: flex;
  flex-direction: column;
  background: var(--page-bg);

  .code-required-content {
    // align-items: center;
    // justify-content: space-around;
    width: 100%;
    // max-width: 1260px;
    align-items: center;

    display: flex;

    .wrapper-item-code-required {
      padding-bottom: 2em;
    }
  }
  .title {
    align-self: center;
    font-family: Google Sans;
    font-style: normal;
    font-weight: 900;
    font-size: 36px;
    padding: 10px 0px;
    line-height: 46px;
    display: flex;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    color: #000000;
  }
  .img-banner {
    width: 100%;
    max-height: 450px;
  }
  .img-line {
    margin-bottom: 40px;
  }
  .box-router {
    height: 37px;
    display: flex;
    align-items: center;
  }
  .custom-button {
    background-color: var(--primary-pink);
    box-shadow: 1px 5px 5px rgba(254, 84, 102, 0.15);
    border-radius: 20px;
    width: 263px;
    height: 44px;
    color: white;
    font-size: 18px;
    line-height: 22px;
    align-self: center;
    margin: 40px 0px;
  }
  .box-button {
    display: flex;
    flex-direction: column;
  }

  @media screen and (min-width: 1260px) {
    .code-required-content {
      align-items: center;
      // justify-content: space-around;
      width: 100%;
      display: flex;
    }
  }
}

.code-required-modal-container {
  display: flex;
  max-width: 500px;
  flex-direction: column;
  .title {
    display: flex;
    margin: 0px 0px 30px 0px;
    justify-content: center;
    p {
      font-size: 23px;
      line-height: 29px;
    }
  }
  .box-date {
    align-items: center;
    .text-date {
      color: #c4a732;
      font-size: 17px;
      line-height: 21px;
      margin-right: 14px;
    }
  }
}

.qr-code-modal-container {
  display: flex;
  max-width: 470px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0px 50px;
  .group-top-content {
    display: flex;
    width: 100%;
    .box-img {
      position: relative;
      margin-top: 4;
      justify-content: center;
      align-items: center;
      display: flex;
    }
    .img {
      height: 60px;
      width: 60px;
      border-radius: 60px;
      object-fit: cover;
      box-shadow: 0px 4px 11px rgba(0, 0, 0, 0.25);
    }
    .box-content {
      margin-left: 14px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      .box-des {
        display: flex;
        justify-content: space-between;
      }
      .text-name {
        color: var(--line-pink);
      }
      .text-shop-name {
        font-size: 17px;
        line-height: 21px;
      }
      .text-sale {
        font-size: 15px;
        line-height: 20px;
        color: var(--primary-pink);
      }
    }
  }
  .group-center-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-top: 10px;
    .info {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }
    .box-icon {
      display: flex;
      width: 100px;
      align-items: center;
      .icon {
        width: 100%;
        height: auto;
      }
    }
    .address {
      font-size: 15px;
      line-height: 18px;
    }
    .booked {
      font-size: 15px;
      line-height: 19px;
      font-weight: 600;
      margin: 10px 0px 5px 0px;
    }
  }
  .text-title-modal {
    font-size: 23px;
    line-height: 27px;
    color: var(--primary-yellow-dark);
    margin: 10px 0px;
  }
  .text-code-modal {
    font-size: 20px;
    line-height: 24px;
    text-transform: none;
  }
  .img-person {
    width: 120px;
    height: 120px;
    align-self: center;
  }
  .button-cancel {
    height: 44px;
    width: 207px;
    border-radius: 4px;
    background-color: var(--white);
    border: 1px solid var(--primary-pink);
    margin-top: 20px;
  }
  .button-cancel-title {
    color: var(--primary-pink);
    font-size: 16px;
    line-height: 20px;
  }
  .box-input {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 30px;
  }
  .text-code {
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    color: #000000;
    font-weight: 700;
    margin-top: 10px;
  }
  h1 {
    font-size: 28px;
    line-height: 24px;
    font-weight: 700;
    margin: 10px 0px;
    text-transform: uppercase;
  }
  span {
    width: 295px;
    font-size: 15px;
    line-height: 20px;
    text-align: center;
    color: #000000;
    margin: 10px 0px;
  }
  img {
    width: 106px;
    height: 106px;
    object-fit: contain;
  }
  .title-input {
    font-size: 15px;
    line-height: 19px;
    opacity: 0.5;
    margin-bottom: 5px;
    align-self: normal;
  }
  input {
    width: 100%;
    height: 44px;
    border: 1px solid var(--border-secondary);
    border-radius: 6px;
    font-size: 15px;
    line-height: 20px;
    border-radius: 4px;
  }
}

@media screen and (max-width: 600px) {
  .qr-code-modal-container {
    padding: 0 10px;
  }
}

.confirm-modal-container {
  display: flex;
  max-width: 500px;
  min-height: 223px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .img-person {
    width: 120px;
    height: 120px;
    align-self: center;
  }
  .button-apply {
    height: 48px;
    width: 207px;
    border-radius: 6px;
  }
  .button-apply-title {
    color: var(--white);
    font-size: 16px;
    line-height: 20px;
  }
  .title-confirm {
    font-size: 23px;
    line-height: 29px;
    text-align: center;
    color: #37c86f;
    margin-top: 16px;
    &.failure {
      color: #fe5466;
    }
  }
  .des-confirm {
    font-size: 15px;
    line-height: 20px;
    text-align: center;
    color: #3a3a3c;
    margin: 16px 0px 20px 0px;
  }
}

@media screen and (min-width: 1260px) {
  .code-required-container {
    align-items: center;
    width: 100%;
    align-items: center;
    display: flex;
  }
}
