.service-history-container {
  .history_service_wrapper {
    display: flex;
    flex-direction: column;
  }
  display: flex;
  flex-direction: column;
  background: var(--page-bg);
  .content {
    width: 80%;
    margin: 0 auto;
  }
  .title {
    text-transform: uppercase;
  }
  .img-banner {
    width: 100%;
    max-height: 450px;
  }
  .img-line {
    margin-bottom: 40px;
  }
  .box-router {
    height: 37px;
    display: flex;
    align-items: center;
  }
  .custom-button {
    background-color: var(--primary-pink);
    box-shadow: 1px 5px 5px rgba(254, 84, 102, 0.15);
    border-radius: 20px;
    width: 263px;
    height: 44px;
    color: white;
    font-size: 18px;
    line-height: 22px;
    align-self: center;
    margin: 40px 0px;
  }
  .box-button {
    display: flex;
    flex-direction: column;
  }
  .MuiTabs-indicator {
    background-color: var(--line-pink);
    height: 3px;
  }
  .Mui-selected {
    .MuiTab-wrapper {
      color: var(--text-primary);
      font-size: 15px;
      font-weight: 700;
    }
  }
  .MuiTab-wrapper {
    color: var(--text-primary);
  }
  .MuiPaper-elevation4 {
    box-shadow: none;
    background-color: transparent;
  }

  .box-history-voucher {
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    width: 100%;
    .text-history {
      font-size: 15px;
      line-height: 19px;
      padding: 0px 10px;
      font-weight: bold;
    }
  }
  @media screen and (max-width: 600px) {
    .content {
      width: 95%;
    }
  }
}

.history-voucher-modal-container {
  display: flex;
  max-width: 500px;
  max-height: 640px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
  .list_voucher_container {
    overflow: scroll;
  }
  .title {
    font-size: 23px;
    line-height: 29px;
    text-align: center;
    color: #adb1b9;
    margin: 26px 0px;
  }
  .box-item {
    display: flex;
    align-items: center;

    box-shadow: inset 0px -1px 0px #eff0f3;
    padding: 14px 0px;
    .box-icon-check {
      justify-content: center;
      align-items: center;
      border-radius: 28px;
      display: flex;
      width: 28px;
      height: 28px;
      box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
      margin: 0px 16px;
    }
    .title-voucher {
      font-size: 15px;
      line-height: 20px;
      color: #24272b;
    }
    .name-voucher {
      font-size: 13px;
      line-height: 16px;
      color: #3e4a59;
    }
    .date-voucher {
      font-size: 13px;
      line-height: 15px;
      letter-spacing: -0.02px;
      color: #9ba9bb;
    }
  }
}
