.item-delivery-history {
  display: flex;
  flex-direction: column;
  margin-bottom: 8px;
  box-shadow: 0px 4px 11px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  margin: 0 auto;

  .item_delivery_history_content {
    padding: 16px;

  }
  .group-top-content {
    display: flex;
    align-items: center;
    width: 100%;
    .box-img {
      position: relative;
      margin-top: 4;
      justify-content: center;
      align-items: center;
      display: flex;
    }
    .img {
      height: 72px;
      width: 72px;
      border-radius: 72px;
      object-fit: contain;
      box-shadow: 0px 4px 11px rgba(0, 0, 0, 0.25);
    }
    .box-content {
      margin-left: 14px;
      .text-name {
        color: var(--line-pink);
      }
      h1 {
        font-size: 15px;
        line-height: 18px;
        color: #000000;
        font-weight: 600;
        padding-bottom: 6px;
      }
      p {
        font-size: 15px;
        line-height: 18px;
        text-transform: uppercase;
        font-weight: 600;
      }
    }
  }
  .deal_name{
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
  }
  
  .merchant-name  {
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 17px;
    color: var(--line-pink);
  }

  .address {
    font-size: 15px;
    line-height: 18px;
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
  }

  .booked {
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-size: 15px;
    line-height: 19px;
    font-weight: 600;
    margin: 10px 0px 5px 0px;
  }
  .group-bottom-button {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding-top: 10px;
  }
  .group-center-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-top: 10px;
    padding-left: 14px;
    border-left: 4px solid var(--line-pink);
    .info {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }
    .box-icon {
      display: flex;
      flex:4;
      align-items: center;
    }
   
  }

  .item_content {
    flex-direction: row;
    display: flex;
    width: 100%;
    align-items: center;
    margin-top: 10px;
  }

  .item_content_title {
    color: #000000;
    opacity: 0.5;
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-size: 15px;
    flex: 4;
    margin-left: 10px;
    line-height: 19px;
    font-weight: 400;
  }

  .total_money_title {
    opacity: 1;
    font-weight: 600;
  }

  .item_content_data {
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-size: 15px;
    flex: 6;
    margin-left: 10px;
    line-height: 19px;
    font-weight: 400;
  }

  .icon {
    path {
      fill: #162A4C;
    }
  } 

  .button-use {
    width: 160px;
    &-title {
      font-size: 15px;
      line-height: 18px;
    }
  }
  .button-detail {
    margin-left: 10px;
    width: 160px;
    background-color: var(--white);
    border: 1px solid var(--primary-yellow-dark);
    &-title {
      font-size: 15px;
      line-height: 18px;
      color: var(--primary-yellow-dark);
    }
  }
}

.box-empty {
  display: flex;
  margin-bottom: 8px;
  width: 370px;
  background-color: transparent;
}
