.voucher-history-container {
  display: flex;
  flex-direction: column;
  background: var(--page-bg);

  .content {
    align-items: center;
    // justify-content: space-between;
    width: 100%;
    max-width: 1260px;
    align-items: center;
    display: flex;
    padding: 0 2em 0 2em;
  }
  .title {
    align-self: center;
    font-family: Google Sans;
    font-style: normal;
    font-weight: 900;
    font-size: 36px;
    padding: 10px 0px;
    line-height: 46px;
    display: flex;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    color: #000000;
  }
  .img-banner {
    width: 100%;
    max-height: 450px;
  }
  .img-line {
    margin-bottom: 40px;
  }
  .box-router {
    height: 37px;
    display: flex;
    align-items: center;
  }
  .custom-button {
    background-color: var(--primary-pink);
    box-shadow: 1px 5px 5px rgba(254, 84, 102, 0.15);
    border-radius: 20px;
    width: 263px;
    height: 44px;
    color: white;
    font-size: 18px;
    line-height: 22px;
    align-self: center;
    margin: 40px 0px;
  }
  .box-button {
    display: flex;
    flex-direction: column;
  }

  @media screen and (min-width: 1260px) {
    .content {
      align-items: center;
      // justify-content: space-between;
      width: 100%;
      align-items: center;
      display: flex;
    }
  }
}

.voucher-modal-container {
  display: flex;
  max-width: 500px;
  max-height: 500px;
  overflow: scroll;
  flex-direction: column;
  .title {
    display: flex;
    margin: 0px 0px 30px 0px;
    justify-content: center;
    p {
      font-size: 23px;
      line-height: 29px;
    }
  }
  .box-date {
    align-items: center;
    .text-date {
      color: #c4a732;
      font-size: 17px;
      line-height: 21px;
      margin-right: 14px;
    }
  }
}

.use-voucher-container {
  display: flex;
  max-width: 500px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .img-person {
    width: 120px;
    height: 120px;
    align-self: center;
  }
  .button-apply {
    height: 44px;
    border-radius: 0px 6px 6px 0px;
  }
  .button-apply-title {
    color: var(--white);
    font-size: 16px;
    line-height: 20px;
  }
  .box-input {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .text-code {
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    color: #000000;
    font-weight: 700;
    margin-top: 10px;
  }
  h1 {
    font-size: 28px;
    line-height: 24px;
    font-weight: 700;
    margin: 10px 0px;
    text-transform: uppercase;
  }
  span {
    width: 295px;
    font-size: 15px;
    line-height: 20px;
    text-align: center;
    color: #000000;
    margin: 10px 0px;
  }
  img {
    width: 106px;
    height: 106px;
    object-fit: contain;
  }

  .error_message {
    margin-bottom: 8px;
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    color: #ff4355;
    font-size: 15px;
  }

  .input-code {
    width: 100%;
    height: 44px;
    border: 1px solid var(--border-secondary);
    border-radius: 6px;
    font-size: 15px;
    line-height: 20px;
    padding: 0px 10px;
    border-radius: 6px 0px 0px 6px;
  }
}

@media screen and (min-width: 1260px) {
  .voucher-history-container {
    align-items: center;
    width: 100%;
    align-items: center;
    display: flex;
  }
}
